export default "<!DOCTYPE html>\r\n<html lang=\"en\" style=\"height: 100%\">\r\n\r\n<head>\r\n    <title>Chart Creator</title>\r\n    <script type=\"text/javascript\" src=\"https://cdn.jsdelivr.net/npm/echarts@4/dist/echarts.min.js\"></script>\r\n</head>\r\n\r\n<body style=\"height: 100%; margin: 0\">\r\n    <div id=\"echarts\" style=\"height: 100%;\"></div>\r\n    <script>\r\n        var option = \r\n;//line 11, replace target\r\n    </script>\r\n    <script>\r\n        if (option.customization.theme !== \"default\") {\r\n            fetch(\"https://raw.githubusercontent.com/apache/incubator-echarts/4.9.0/theme/\" + option.customization.theme + \".js\")\r\n            .then(response => response.text())\r\n            .then(data => {\r\n                eval(data);\r\n                echarts.init(document.getElementById(\"echarts\"), option.customization.theme).setOption(option);\r\n            });\r\n        }\r\n        else {\r\n            echarts.init(document.getElementById(\"echarts\")).setOption(option);\r\n        }\r\n\r\n    </script>\r\n\r\n</body>\r\n\r\n</html>"
